import appFormButton from '../app-form-id/app-form-id.vue';

export default {
    name: 'app-prompt-dialog',
    components: {
        "app-form-button": appFormButton,
    },
    props: {
        content: String,
    },
    computed: {},
    methods: {
        handleClick(boolean) {
            this.$emit('toastModelClick', boolean);
        },
        copyText() {
            this.$store.dispatch('gConfig/reportAndErrorB', false);

            this.$utils.uniCopy({
                data: this.content
            });






        },
        refreshPage() {
            this.$store.dispatch('gConfig/reportAndErrorB', false);
        }
    }
}
